import { Paper, Typography, Stack, Button } from "@mui/material";
import { useState } from "react";
import { Link } from "react-router-dom";
import Email from "@mui/icons-material/EmailOutlined";
import Lock from "@mui/icons-material/LockOutlined";
import Phone from "@mui/icons-material/Phone";
import Eyes from "@mui/icons-material/RemoveRedEyeSharp";
import Eye from "@mui/icons-material/RemoveRedEyeOutlined";
import { useDispatch, useSelector } from "react-redux";
import { login } from "../redux/apiCall";

function Register() {
  const [lock, setLock] = useState(true);
  const [email, setEmail] = useState("");
  const [pwd, setPwd] = useState("");
  const dispatch = useDispatch();
  const { isFetching, error } = useSelector((state) => state.user);

  const handelLock = () => setLock(!lock);
  const handelEmail = (e) => setEmail(e.target.value);
  const handelPwd = (e) => setPwd(e.target.value);
  const handelClick = () => {
    setEmail("");
    setPwd("");
    login(dispatch, { email, pwd });
  };

  return (
    <div className="authContainer">
      <Paper
        elevation={3}
        sx={{ p: 2, width: { xs: "100%", md: "30.33%" }, borderRadius: 2 }}
      >
        <Stack spacing={2}>
          <Typography className="h1">Salut, bienvenu!</Typography>
          <Typography className="h2">
            S'il vous plait Entrez vos information pour vous Enregistrer
          </Typography>
          <label className="label-form">Email</label>
          <div className="form-control">
            <Email className="icon" />
            <input
              type="text"
              placeholder="enter your email"
              className="login-form"
              value={email}
              onChange={handelEmail}
            />
          </div>
          <label className="label-form">Telephone</label>
          <div className="form-control">
            <Phone className="icon" />
            <input
              type="text"
              placeholder="enter your phone"
              className="login-form"
              value={email}
              onChange={handelEmail}
            />
          </div>
          <label className="label-form">Password</label>
          <div className="form-control">
            <Lock className="icon" />
            <input
              type={lock ? "password" : "text"}
              placeholder="enter your password"
              className="login-form"
              value={pwd}
              onChange={handelPwd}
            />
            {lock ? (
              <Button onClick={handelLock}>
                <Eyes className="icon" />
              </Button>
            ) : (
              <Button onClick={handelLock}>
                <Eye className="icon" />
              </Button>
            )}
          </div>
          <Typography color="gray">
            <Link to="/register" className="link">
              Forget Password?
            </Link>
          </Typography>
          <Button
            fullWidth
            onClick={handelClick}
            className="submit-btn"
            disabled={isFetching}
          >
            Login
          </Button>
          {error && (
            <Typography className="error">
              Please verified your information
            </Typography>
          )}
          <Typography className="h3">
            pas encore inscrit?{" "}
            <Link to="/register" className="link">
              Inscription
            </Link>
          </Typography>
        </Stack>
      </Paper>
    </div>
  );
}

export default Register;
