import { Box, Paper, Typography, Avatar } from "@mui/material";
import NewMessage from "../Components/NewMessage";
import NewMessageIcon from "@mui/icons-material/Message";
import Search from "@mui/icons-material/Search";
import Discussion from "../Components/Discussion";
import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { useNavigate, Link, useParams } from "react-router-dom";
import UserImage from "../Assets/Images/photo pro.jpg";

function Discussions() {
  const { currentUser } = useSelector((state) => state.user);
  const navigate = useNavigate();
  let { id } = useParams();
  const [search, setSearch] = useState("");
  const [newMsg, setNsg] = useState(false);
  const discussions = [
    {
      id: "0",
      user: "mazeking",
      img: "12341234.jpg",
      message: {
        text: "I want to see you.",
        statut: "send",
        date: "14:12",
        sender: "me",
      },
    },
    {
      id: "1",
      user: "soupnazie21",
      img: "12341234.jpg",
      message: {
        text: "I want to see you.",
        statut: "recieved",
        date: "11:42",
        sender: "me",
      },
    },
    {
      id: "2",
      user: "soupnazie21",
      img: "12341234.jpg",
      message: {
        text: "I want to see you.",
        statut: "read",
        date: "11:42",
        sender: "me",
      },
    },
  ];
  if (id) console.log(id);

  const handelChange = (e) => setSearch(e.target.value);
  const handelNewMsg = () => setNsg(!newMsg);
  useEffect(() => {
    if (currentUser == null) {
      navigate("../login");
    }
  });
  return (
    <div className="discussion">
      {newMsg ? (
        <NewMessage  handelClick={handelNewMsg}/>
      ) : (
        <div>
          <Paper elevation={3} square>
            <div className="header">
              <Typography variant="h6" className="top-bar-text">
                MazeChat
              </Typography>
              <Box>
                {/* <Link to="setting" className="link">
              <Settings className="icon" />
            </Link> */}
                <Link to="setting" className="link">
                  <Avatar src={UserImage} alt="NGUEMBU YAL" />
                </Link>
              </Box>
            </div>
          </Paper>
          <Box className="discussionList">
            <Box className="searchGroup">
              <Search className="searchIcon" />
              <input
                placeholder="search.."
                className="search"
                value={search}
                onChange={handelChange}
              />
            </Box>
            {discussions.map((discussion) => (
              <Discussion discussion={discussion} key={discussion.id} />
            ))}
          </Box>
          <button className="new-msg-btn" onClick={handelNewMsg}>
            <NewMessageIcon />
          </button>
        </div>
      )}
    </div>
  );
}

export default Discussions;
