import { Box, Typography } from "@mui/material";
import OneTick from "@mui/icons-material/Check";
import DoubleTick from "@mui/icons-material/CheckCircle";
import Img from "../Assets/Images/Angular.png";
import { Link } from "react-router-dom";

const Discussion = ({ discussion }) => {
  const { id, user /*, img*/, message } = discussion;
  const { text, statut, date /* sender */ } = message;

  return (
    <Box className="item">
      <Link className="link" to={id}>
        <Box className="flexbox">
          <Box>
            <img src={Img} alt="user"/>
          </Box>
          <Box className="text-box">
            <Typography variant="h6" className="username">
              {user}
              <span className="date">
                {date + " "}
                {statut === "send" ? (
                  <OneTick className="icon" />
                ) : statut === "recieved" ? (
                  <>
                    <DoubleTick className="icon recieved" />
                  </>
                ) : (
                  <>
                    <DoubleTick className="icon" />
                  </>
                )}
              </span>
            </Typography>
            <Typography className="msg-text">{text}</Typography>
          </Box>
        </Box>
      </Link>
    </Box>
  );
};

export default Discussion;
