import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import Message from "../Components/Message";
import { publicRequest } from "../requestMethod";
import { Box, Button, Paper, Typography } from "@mui/material";
import Send from "@mui/icons-material/Send";
import Call from "@mui/icons-material/Phone";
import Menu from "@mui/icons-material/Menu";
import Emoji from "@mui/icons-material/EmojiEmotions";

function Conversations() {
  const navigate = useNavigate();
  const { currentUser } = useSelector((state) => state.user);
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([
    {
      id: "0",
      text: "I want to see you. rjkdnkbxclbmmmmmmmmmmm,.z   djhxdxni dn jnnnn  dsjnkjkbjfajkv baxzbhsdfvbsbvhwe <Br>   bvvjcd,   db dfg  ",
      statut: "send",
      date: "14:12",
      sender: "1",
    },
    {
      id: "1",
      text: "I want to see you.",
      statut: "send",
      date: "14:12",
      sender: "1",
    },
    {
      id: "2",
      text: "I want to see you.",
      statut: "send",
      date: "14:12",
      sender: "me",
    },
    {
      id: "3",
      text: "I want to see you.",
      statut: "send",
      date: "14:12",
      sender: "you",
    },
    {
      id: "4",
      text: "I want to see you. rjkdnkbxclbmmmmmmmmmmm,.z   djhxdxni dn jnnnn  dsjnkjkbjfajkv baxzbhsdfvbsbvhwe <Br>   bvvjcd,   db dfg  ",
      statut: "send",
      date: "14:12",
      sender: "1",
    },
    {
      id: "5",
      text: "I want to see you.",
      statut: "send",
      date: "14:12",
      sender: "1",
    },
    {
      id: "6",
      text: "I want to see you.",
      statut: "send",
      date: "14:12",
      sender: "me",
    },
    {
      id: "7",
      text: "I want to see you.",
      statut: "send",
      date: "14:12",
      sender: "you",
    },
  ]);

  const handelMessage = (e) => {
    setMessage(e.target.value);
  };

  const handelKeyUp = (e) => {
    if (e.key === "Enter") sendMessage();
  };

  const getMessage = () => {
    publicRequest
      .get("/message/" + currentUser)
      .then((message) => message.data)
      .then((message) => setMessages(message));
  };

  useEffect(() => {
    if (currentUser === null) {
      navigate("../login");
    }
    getMessage();
  },[messages]);

  const sendMessage = () => {
    let date = new Date();
    date = date.getFullYear() + "-" + date.getMonth() + "-" + date.getDate();
    const msg = {
      text: message,
      statut: "send",
      date:"2022-06-17",
      sender: currentUser.id,
      receiver: "1",
    };
    console.log(msg);
    publicRequest.post("/message/", msg);
    setMessages([...messages, msg]);
    setMessage("");
  };

  let { id } = useParams();
  return (
    <div className={id !== "" ? "conversation id" : "conversation"}>
      <Paper elevation={3} square>
        <div className="flexbox">
          <Typography variant="h6" className="topBarText">
            {"Mazeking"}
          </Typography>
          <div className="">
            <Button>
              <Call />
            </Button>
            <Button>
              <Menu />
            </Button>
          </div>
        </div>
      </Paper>
      <Box className="messages-list">
        {messages.map((message, index) => (
          <Message message={message} key={index} />
        ))}
      </Box>
      <Box className="flexbox textZone">
        <Button>
          <Emoji fontSize="large" color="primary" />
        </Button>
        <input
          placeholder="Saisissez votre message.."
          className="type"
          value={message}
          onChange={handelMessage}
          onKeyUp={handelKeyUp}
        />
        <Button onClick={sendMessage}>
          <Send fontSize="large" color="primary" />
        </Button>
      </Box>
    </div>
  );
}

export default Conversations;
