import { Box, Paper, Typography, Button } from "@mui/material";
import OneTick from "@mui/icons-material/Check";
import DoubleTick from "@mui/icons-material/CheckCircle";
import Delete from "@mui/icons-material/Delete";
import { useState } from "react";

const Message = ({ message, }) => {
  const { text, statut, date, sender } = message;
  const [option, setOption] = useState(false);
  const handelOption = () => {
    setOption(!option);
    window.event.returnValue = false;
  };
  const closeContextMenu = () => {
    if (option) handelOption();
  };
  document.addEventListener("click", () => closeContextMenu());

  return (
    <Box
      style={{
        width: "100%",
        display: "block",
        float: "left",
      }}
    >
      <Box
        className={sender === "me" ? "message me" : "message notme"}
        onContextMenu={handelOption}
        contextmenu=""
      >
        <Typography>
          {text}
          <span
            style={{
              float: "right",
              color: "GrayText",
              fontSize: 13,
              marginTop: "3px",
            }}
          >
            {date + " "}
            {statut === "send" ? (
              <OneTick
                fontSize="small"
                color="gray"
                style={{
                  fontSize: "19px",
                  verticalAlign: "middle",
                }}
              />
            ) : statut === "recieved" ? (
              <>
                <DoubleTick fontSize="small" color="gray" />
                <DoubleTick fontSize="small" color="gray" />
              </>
            ) : (
              <>
                <DoubleTick fontSize="small" color="primary" />
                <DoubleTick fontSize="small" color="primary" />
              </>
            )}
          </span>
        </Typography>
        {option ? (
          <Paper className="menu">
            <Button className="element">
              Delete <Delete fontSize="small" className="icon" />
            </Button>
            {/* <Button className="element">
              Delete <Delete fontSize="small" className="icon" />
            </Button> */}
          </Paper>
        ) : (
          ""
        )}
      </Box>
    </Box>
  );
};
export default Message;
