import { Box, Typography } from "@mui/material";
import Img from "../Assets/Images/Angular.png";
import { Link } from "react-router-dom";

const Contact = ({ contact, handelClick }) => {
  const { id, user, lastSeen } = contact;

  return (
    <Box className="item">
      <Link className="link" to={id} onClick={handelClick}>
        <Box className="flexbox">
          <Box>
            <img src={Img} alt="userimage" />
          </Box>
          <Box className="text-box">
            <Typography variant="h6" className="username">
              {user}
            </Typography>
            <Typography className="msg-text">Seen at {lastSeen}</Typography>
          </Box>
        </Box>
      </Link>
    </Box>
  );
};

export default Contact;
